.main{
  padding: 5.375em 0 9em;
  flex-grow: 1;
  background-size: cover;
  background-position: center;
  &__inner{
    max-width: 53.125em;
  }
  &__title{
    color: #fff;
    text-shadow: 0 .25em .25em rgba(0, 0, 0, 0.25);
  }
  .filter{
    margin-top: 7.25em;

  }
}


.filter{
  display: flex;
  align-items: center;
  &__fields{
    background-color: #fff;
    border-radius: .375em;
    display: flex;
    align-items: center;
    flex-grow: 1;
    @media screen and (min-width: 993px){
      .fg{
        input,
        .m-select .current{
          background-color: transparent;
          box-shadow: none;
        }
      }
      .filter__radios{
        background-color: transparent;
        box-shadow: none;
      }
    }

  }
  &__btn{
    flex-shrink: 0;
    margin-left: .625em;
  }
  &__type{
    width: 8.75em;
    flex-shrink: 0;
  }
  &__address{
    flex-grow: 1;
  }
  &__radios{
    display: flex;
    align-items: center;
    padding: 0 .3125em;
    flex-shrink: 0;
    height: 2.25em;
    border-left: $border;
    border-right: $border;
  }
  &__radio{
    position: relative;
    padding: 0 .625em;
    &:not(:last-child){
      border-right: $border;
    }
    input{
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      left: 0;
      top: 0;
      &:checked+label{
        color: $primary;
      }
    }
    label{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 3.125em 0 6.25em;
    &__title{
      text-align: center;
    }

    .filter{
      margin-top: 2.5em;
    }
  }

  .filter{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    &__fields{
      display: grid;
      grid-template-columns: 100%;
      gap: .625em;
      background-color: transparent;
      padding: 0;
    }
    &__type{
      width: 100%;
    }
    &__radios{
      background-color: #fff;
      box-shadow: 0 0 10px rgba(51,51,51,.2);
      border-radius: .375em;
      height: 2.875em;
      padding: 0 1em;
      border: none;
    }
    &__radio{
      padding: 0 1.5em;
      &:nth-child(1){
        padding: 0 1.25em 0 0;
      }
      &:last-child{
        padding: 0 0 0 1.25em;
      }
    }
    &__btn{
      margin: 0;
    }
  }
}
