@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-accent{
    padding: 0 1.25em;
    height: 2.875em;
    background-color: $primary;
    color: #fff;
    border-radius: .375em;
    span{
        font-size: 1em;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
    &.shadow{
        box-shadow: 0 .25em .25em rgba(0, 0, 0, 0.25);
    }
}
