.fg {
  position: relative;
  margin: 0;
  font-size: 1em;
  input,
  select,
  textarea,
  .m-select .current{
    border: none;
    box-shadow: 0px 0px 10px rgba(51, 51, 51, 0.2);
    border-radius: .375em;
    padding: 0 2.5em 0 1em;
    height: 2.875em;
    width: 100%;
    font-size: 1em;
    line-height: inherit;
    color: $black;
    background-color: #FFFFFF;
    &::placeholder {
      color: $gray;
    }
    &:hover,
    &:focus{
      border-color: $primary;
    }
  }
  .m-select{
    height: 2.875em;
    width: 100%;
    font-size: 1em;
    padding: 0;
    background-color: transparent !important;
    border: none !important;
    float: unset;
    .current{
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
