    }
    &.open{
      .current{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_4241_186572)'%3E%3Cpath d='M5.99078 2.84089C5.89228 2.84074 5.79471 2.86009 5.70371 2.8978C5.61271 2.93551 5.53006 2.99085 5.46053 3.06064L0.210534 8.31063C0.0739153 8.45208 -0.00168046 8.64154 2.8352e-05 8.83818C0.00173717 9.03483 0.0806138 9.22294 0.21967 9.362C0.358726 9.50105 0.546835 9.57993 0.743483 9.58164C0.94013 9.58335 1.12958 9.50775 1.27103 9.37113L5.99078 4.65176L10.7105 9.37113C10.7797 9.44276 10.8625 9.4999 10.954 9.53921C11.0455 9.57851 11.1439 9.5992 11.2435 9.60007C11.3431 9.60093 11.4418 9.58196 11.534 9.54425C11.6262 9.50654 11.7099 9.45085 11.7803 9.38043C11.8507 9.31001 11.9064 9.22627 11.9442 9.1341C11.9819 9.04193 12.0008 8.94317 12 8.84358C11.9991 8.744 11.9784 8.64558 11.9391 8.55408C11.8998 8.46258 11.8427 8.37982 11.771 8.31063L6.52103 3.06064C6.4515 2.99085 6.36886 2.93551 6.27786 2.8978C6.18685 2.86009 6.08929 2.84074 5.99078 2.84089V2.84089Z' fill='%23AEAEAE'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_4241_186572'%3E%3Crect width='12' height='12' fill='white' transform='matrix(1 0 0 -1 0 12)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
      }
      .list{
        transform: translateY(0);
      }
    }
    .list{
      transform: translateY(2em);
      padding: 1em;
      background: #fff;
      box-shadow: 0 .25em 1.25em rgba(51, 51, 51, 0.1);
      border-radius: .375em;
      transition: all .3s;
      display: grid;
      grid-template-columns: 100%;
      gap: 1.25em;
      min-width: 100%;
      width: auto;
    }
    .option{
      white-space: nowrap;
      font-size: .875em;
      padding: 0;
      border: none !important;
      font-weight: 500;
      color: $black;
      &:hover,
      &.focus,
      &.selected.focus {
        background-color: transparent;
        font-weight: 500;
        color: rgba($secondary,.6);
      }
      &.disabled{
        display: none;
        font-weight: 400 !important;
        pointer-events: none;
      }
    }
  }
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 1.112em;
    font-size: 1em;
    line-height: inherit;
    top: 50%;
    transform: translateY(-50%);
    color: rgba($black,.6);
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    transition: all .3s;
    width: 1.25em;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: #ADADAD;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $black;
      }
      &~.rating-input__item {
        path{
          fill: $black;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"] .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"] .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"] .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"] .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"] .rating-input__item:nth-child(n+1) {
  path{
    fill: $black;
  }
}
