.logo{
  display: block;
  width: 8.125em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header,
.footer{
  padding: .6875em 0;
  background-color: #fff;
  box-shadow: 0 .25em 1.25em rgba(51, 51, 51, 0.1);
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.menu{
  display: flex;
  align-items: center;
  &__user{
    display: flex;
    align-items: center;
    .avatar{
      width: 2.875em;
      height: 2.875em;
      margin-left: .625em;
    }
  }
  &__logout{
    margin-left: 1.875em;
    text-decoration-line: underline;
    color: $red;
  }
  &__bg{
    display: none;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 576px){
  .header,
  .footer{
    padding: .875em 0;
    &__btn{
      display: none;
    }
  }

  .header{
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
      .burger{
        background-image: url(../img/burger2.svg);
        background-size: 1.5em auto;
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 100;
    height: 100vh;
    width: 17.1875em;
    background-color: #fff;
    flex-direction: column;
    padding: 4.675em 1.25em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg{
      position: fixed;
      z-index: 99;
      background: rgba(0, 0, 0, 0.4);
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
    }
    &__user{
      margin-right: auto;
    }
    &__logout{
      margin: 1.875em auto 0 0;
    }
  }

  .burger{
    display: block;
    width: 1.875em;
    height: 1.5em;
    background-image: url(../img/burger.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
    position: relative;
    z-index: 101;
    transition: all .3s;
  }
}
