@import "../mixins";
.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;

    @media screen and (min-width: 577px) {
        text-align: center;
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
        .modal__dialog {
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }

    &--active {
        opacity: 1;
        pointer-events: unset;
    }

    &__bg {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.4);
    }

    &__dialog {
        margin: auto;
        background-color: #fff;
        width: 31.25em;
        max-width: calc(100% - 2em);
        border-radius: .5em;
        position: relative;
    }
    &__top,
    &__content{
        padding: 1.875em;
    }
    &__top{
        border-bottom: $border;
    }
    &__text{
        a{
            color: $primary;
            text-decoration: underline;
        }
    }
    &__btn{
        width: 100%;
        margin-top: 1.25em;
    }
    &__hint{
        margin-top: .5em;
    }
    &__fields{
        @include grid100gap(1.25em);
        .fg{
            input{
                padding: 0 1.375em;
                height: 3.75em;
                border-radius: .5em;
                box-shadow: none;
                border: $border;
            }
            textarea{
                padding: 1.375em;
                height: 8.75em;
                resize: none;
                box-shadow: none;
                border: $border;
            }
        }
    }
    &__or{
        margin: 1.25em 0;
        display: flex;
        align-items: center;
        justify-content: center;
        &:before,
        &:after{
            content: '';
            height: 1px;
            width: auto;
            flex-grow: 1;
            background-color: #D9D9D9;
            display: block;
        }
        span{
            margin: 0 .625em;
            flex-shrink: 0;
        }
    }
    &__map{
        .map{
            height: 17em;
        }
    }
}

.social{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__item{
        width: 3.75em;
        height: 3.75em;
        display: block;
        &:hover{
            img{
                transform: translateY(-.625em);
            }
        }
        img{
            width: 100%;
            transition: all .3s;
        }
    }
}

.modal-login{
    .social{
        margin-bottom: 1.25em;
    }
}

.ratings{
    @include grid100gap(.9375em);
    &__group{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.file{
    margin-top: .375em;
    min-height: 4.5em;
    position: relative;
    &__slider{
        overflow-x: hidden;
        .swiper{
            &-slide{
                position: relative;
                width: 7.1875em;
                padding: .3125em 1.25em 0 0;

            }
        }
    }
    &__upload{
        height: 4.125em;
        width: 100%;
        background-color: rgba(18, 60, 255, 0.1);
        border-radius: .375em;
        color: $secondary;
        position: relative;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 1.25em auto;
        input{
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 2;
            cursor: pointer;
            width: 100%;
            height: 100%;
        }
    }
    &._has-file{
        .file__upload{
            z-index: 2;
            width:  4.125em;
            position: absolute;
            right: 0;
            bottom: 0;
            background-image: url(../img/plus.svg);
            span{
                display: none;
            }
        }

    }
}

.img-file{
    &>img{
        width: 100%;
        height: 4.125em;
        border-radius: .5em;
        object-fit: cover;
    }
    &__remove{
        position: absolute;
        top: 0;
        right: .9375em;
        background: #E7ECFF;
        width: 1.25em;
        height: 1.25em;
        border-radius: 50%;
        img{
            width: .5em;
        }
        &:hover{
            background: darken(#E7ECFF,10%);
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 4em 0;
        &__top,
        &__content{
            padding: 1.25em;
        }
        &__fields{
            gap: .625em;
            .fg{
                input{
                    padding: 0 1em;
                    height: 3em;
                }
                textarea{
                    padding: 1em;
                    height: 7.5em;
                }
            }
        }
        &__hint{
            margin-top: .7em;
        }
        &__map{
            .map{
                height: 11.375em;
            }
        }
    }

    .social{
        &__item {
            width: 2.5em;
            height: 2.5em;
        }
    }
}
