@import "../mixins";
.apart{
  padding: 2.5em 0 4.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__reviews{
    display: flex;
    align-items: center;
    .rating-num{
      margin-right: 1.25em;
    }
  }
  &__desc{
    text-align: right;
    a{
      color: $primary;
      text-decoration: underline;
    }
  }
}

.gallery{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  //grid-template-rows: 1fr 1fr;
  gap: 1.875em;
  grid-auto-flow: dense;
}

.gallery-card{
  &:nth-child(1){
    grid-column: 1/3;
    grid-row: 1/3;
  }
  &:nth-child(5)~.gallery-card{
    display: none;
  }
  &__img{
    display: block;
    @include imgCover(62%);
    min-height: 100%;
    img{
      border-radius: .5em;
    }
  }
}


.stat,
.feedback{
  margin-top: 3.125em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,34.0625em);
    gap: 1.875em;
    justify-content: space-between;
  }
}

.stat{
   &__top{
    display: flex;
    .rating-num{
      margin-right: 1.875em;
    }
  }
  &__items{
    margin-top: 1.875em;
  }
}

.stat-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__nums{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-left: 1em;
  }
  &__progress{
    width: 9.375em;
    height: .25em;
    background: #D9D9D9;
    border-radius: .25em;
    position: relative;
    &-fill{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      border-radius: inherit;
      background-color: $black;
    }
  }
  &__ball{
    margin-left: .9375em;
    text-align: right;
    min-width: 1.625em;
  }
}

.feedback{
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
}

.feedback-card{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__user{
    display: flex;
    align-items: center;
    &-date{
      margin-top: .3125em;
    }
  }
  .avatar{
    margin-right: .625em;
  }
  &__text{
    margin-top: .9375em;
    line-height: 130%;
    p{
      line-height: inherit;
    }
  }
  &__images{
    margin-top: .625em;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__img{
    width: 6.25em;
    margin: .3125em .625em 0 0;
    &-wrap{
      display: block;
      @include imgCover(68%);
      img{
        border-radius: .25em;
      }
    }
  }
}

.avatar{
  width: 3em;
  height: 3em;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    display: block;
  }
}

.like{
  display: flex;
  align-items: center;
  cursor: pointer;
  &__icon{
    display: block;
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    margin-right: .9375em;
    background-image: url(../img/like.svg);
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    transition: all .3s;
  }
  &._active{
    .like__icon{
      background-image: url(../img/like2.svg);
    }
  }
}


@media screen and (max-width: 992px){
  .apart{
    &__desc{
      max-width: 16em;
      br{
        display: none;
      }
    }
  }

  .gallery{
    gap: .625em;
  }

  .stat,
  .feedback{
    &__items{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .stat{
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .apart{
    padding: 3.125em 0;
    &__top{
      @include grid100gap(1.25em);
    }
    &__desc{
      text-align: left;
    }
  }

  .gallery{
    gap: .625em .3125em;
    margin-top: 1.25em;
  }

  .gallery-card{
    &:nth-child(1){
      grid-column: 1/-1;
      grid-row: unset;
      .gallery-card__img{
        padding-bottom: 64%;
      }
    }
  }

  .stat-card{
    &__progress{
      width: 7.5em;
    }
  }

  .feedback{
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
}
