@import "../mixins";
.search{
  padding: 2.5em 0 6em;
  .filter{
    max-width: 53.125em;
    margin-top: 1.875em;
    &__fields{
      box-shadow: 0 0 .625em rgba(51, 51, 51, 0.2);
    }
    &__btn{
      box-shadow: 0 .25em .25em rgba(0, 0, 0, 0.25);
    }
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 27.5em calc(100% - 27.5em - 1.25em);
    gap: 1.25em;
  }
  &__items{
    padding-right: .625em;
    overflow-y: auto;
    height: 34.375em;
    @include scroll;
  }
}

.map{
  height: 34.375em;
  border-radius: .5em;
  overflow: hidden;
  .ymaps-2-1-79-balloon{
    opacity: 0 !important;
    pointer-events: none !important;
  }
  .ymaps-2-1-79-copyright,
  .ymaps-2-1-79-map-copyrights-promo{
    opacity: 0;
  }
  .balloon__icon{
    background: #FECC55;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 21px;
  }
}

.search-card{
  display: grid;
  grid-template-columns: 11em calc(100% - 11.625em);
  justify-content: space-between;
  padding: 1.25em 0;
  border-top: $border;
  &__img{
    display: block;
    @include imgCover(75%);
    img{
      border-radius: .25em;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__title,
  &__desc{
    @include truncate(2);
  }
  &__desc{
    margin-top: .56em;
  }
  &__bottom{
    padding-top: 1em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.rating-num{
  display: flex;
  align-items: center;
  img{
    width: 1.25em;
    display: block;
    flex-shrink: 0;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .search{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
    &__items{
      padding: 0;
      overflow: unset;
    }
    .filter{
      margin-top: 1.25em;
      &__fields{
        box-shadow: none;
      }
    }
  }

  .map{
    order: -1;
    height: 20em;
  }
}


@media screen and (max-width: 576px){
  .search{
    &__items{
      @include grid100gap(1.25em);
    }
  }

  .search-card{
    grid-template-columns: 100%;
    gap: 1em;
    padding: 0;
    border: none;
    &__img{
      padding-bottom: 61%;
    }
  }

  .map{
    height: 13em;
  }
}
